import React, { useState, useContext } from "react";
import { useMoralis, useNFTBalances, useWeb3ExecuteFunction } from "react-moralis";
import { Modal, Button, Card, Image, Tooltip, Skeleton } from "antd";
import { FileSearchOutlined, ShoppingCartOutlined } from "@ant-design/icons";
import { getExplorer } from "helpers/networks";
import { useVerifyMetadata } from "hooks/useVerifyMetadata";
import { brightFontCol } from "GlobalStyles";
import { Input, Divider } from "antd";
import { Link } from "react-router-dom";
import { mainMarketAddress, deployedABI, listItemFunction } from "../MarketplaceSCMetadata";
import { NFTCardStyle, NFTsDiv, NFTImg, BtnPrimary, NFTImgWrapperStyle } from "../GlobalStyles";
import { AllowedNftContracts } from "../MglNftMetadata";
import { AvatarCtx } from "index";
import { SkinOutlined } from "@ant-design/icons";

const { Meta } = Card;

function NFTBalance() {
  // eslint-disable-next-line no-unused-vars
  const [avatar, setAvatar] = useContext(AvatarCtx);

  const { data: NFTBalances } = useNFTBalances();
  const { chainId } = useMoralis();
  const { verifyMetadata } = useVerifyMetadata();

  const [visible, setVisibility] = useState(false);
  const [nftToList, setNftToList] = useState(null);
  const [listingPrice, setListingPrice] = useState(0);

  const contractABI = deployedABI;
  const marketAddress = mainMarketAddress;

  const contractABIJson = JSON.parse(contractABI)
  const contractProcessor = useWeb3ExecuteFunction();

  const handleListForSaleClick = (nft) => {
    setNftToList(nft);
    console.log(nft.image);
    setVisibility(true);
  };

  function succListing() {
    let secondsToGo = 5;
    const modal = Modal.success({
      title: "Success!",
      content: `You have listed your NFT`,
    });
    setTimeout(() => {
      modal.destroy();
    }, secondsToGo * 1000);
  }

  const listNft = async () => {
    if (listingPrice <= 0) {
      alert("price must be greater then 0");
    }
    const p = listingPrice * ("1e" + 18);
    const ops = {
      contractAddress: marketAddress,
      functionName: listItemFunction,
      abi: contractABIJson,
      params: {
        nftContract: nftToList.token_address,
        tokenId: nftToList.token_id,
        price: String(p)
      }
    };
    console.log('listNft', listingPrice, ops.params, ops.contractAddress);
    await contractProcessor.fetch({
      params: ops,
      onSuccess: () => {
        succListing();
      },
      onError: (err) => {
        alert(err);
      }
    });
  };

  console.log("Filtered NFTBalances", NFTBalances?.result
    .filter(nft => nft.image && nft.token_address === AllowedNftContracts.get(chainId)));

  return (
    <div style={{
      padding: "0 14%",
      // maxWidth: "1030px",
      width: "100%",
      background: "none",
      color: brightFontCol,
    }}>
      <h1 style={{
        fontFamily: "Source Serif Pro",
        textAlign: "center",
        fontSize: "27px",
        fontWeight: "bold",
      }}>Your avatars and wearables <SkinOutlined /></h1>
      <br />
      <h3>
        If you have your MGL Avatar NFT 
      </h3>
      <h3>
        Then you will be albe to {" "}{" "}Play in MetaGymLand Metaverse
      </h3>
      <h3>
        by clicking <b style={{ color: "#1990FF" }}>[Play with me]</b> button{" "}
      </h3>
      <h3>
        If you don't have your awesome Avatar yet, get one in our
        {" "}<Link to="/marketplace">
          <b><u>Marketplace</u></b>
        </Link>{" "}
      </h3>
      {NFTBalances?.result &&
        <Divider style={{ backgroundColor: brightFontCol }} />}
      <div style={NFTsDiv}>
        <Skeleton loading={!NFTBalances?.result}>
          {NFTBalances?.result &&
            NFTBalances.result
              .filter(nft => nft.image && nft.token_address === AllowedNftContracts.get(chainId))
              .map((nft, index) => {
                //Verify Metadata
                nft = verifyMetadata(nft);
                return (
                  <Card
                    hoverable
                    key={`card-${index}`}
                    actions={[
                      <Tooltip title="View On Blockexplorer">
                        <FileSearchOutlined
                          onClick={() => window.open(`${getExplorer(chainId)}address/${nft.token_address}`, "_blank")}
                        />
                      </Tooltip>,
                      <Tooltip title="List for Sale">
                        <ShoppingCartOutlined
                          onClick={() => handleListForSaleClick(nft)}
                        />
                      </Tooltip>,
                    ]}
                    style={NFTCardStyle}
                    cover={
                      <Image
                        preview={false}
                        src={nft?.image || "error"}
                        fallback="data:image/png;base64,"
                        alt=""
                        style={NFTImg}
                        wrapperStyle={{
                          ...NFTImgWrapperStyle,
                          backgroundColor: "#" + nft?.background_color,
                        }}
                      />
                    }
                  >
                    <Meta
                      title={nft.name}
                      description={
                        <>
                          <span><b>#{nft.token_id}</b></span>
                          <p>Amount You Own: <b>{nft.amount}</b></p>
                        </>
                      } />
                    <div
                      style={{
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                        marginTop: "1rem",
                      }}>
                      <Button
                        onClick={() => {
                          const avatarUri = nft?.image;
                          const avatarTokenAddress = nft?.token_address;
                          const avatarTokenId = nft?.token_id;
                          setAvatar({
                            uri: avatarUri,
                            tokenAddress: avatarTokenAddress,
                            tokenId: avatarTokenId,
                          });
                        }}
                        type="primary"
                        style={BtnPrimary}
                      >
                        <Link to='/play-setup'>
                          Play with me
                        </Link>
                      </Button>
                    </div>

                    <Modal
                      title={`List ${nftToList?.name} #${nftToList?.token_id}`}
                      visible={visible}
                      onCancel={() => setVisibility(false)}
                      onOk={() => {
                        listNft()
                        if (listingPrice > 0) setVisibility(false);
                      }}
                      okText="List for Sale"
                    >
                      <img
                        src={nftToList?.image}
                        alt=""
                        style={{
                          width: "250px",
                          margin: "auto",
                          borderRadius: "10px",
                          marginBottom: "15px",
                        }}
                      />
                      {/* <Alert
                        message="This NFT is currently not for sale"
                        type="warning"
                      /> */}
                      <Input
                        autoFocus
                        required
                        placeholder="set price"
                        onChange={e => setListingPrice(e.target.value)}
                      />
                    </Modal>
                  </Card>
                );
              })}
        </Skeleton>
      </div>
    </div>
  );
}

export default NFTBalance;
